
.home-container {
    justify-content: center;
    display: flex;
    align-items: center;
    margin-top: 3%;
  }
  
  .images-container {
    display: flex;
    align-items: center;
    gap: 31px;
    padding: 10px;
    position: relative; /* Add position relative */
  }
  
  .background-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50%; /* Set height to 50% for top half */
    background-image: url('C:\Users\lenovo\Desktop\Travel App\travel-app\travlog\public\Earth.png');
    background-size: cover;
    background-position: center;
  }  
  
  .column {
    display: flex;
    flex-direction: column;
    gap: 31px;
  }
  
  .third-image {
    margin-left: auto;
  }
  
  .rounded-image {
    border-radius: 50%;
    margin-bottom: 10px;
    width: 200px;
    height: 100%;
    border-radius: 32px;
    background: white
  }
  
  .text-section {
    display: flex;
    width: 427px;
    flex-direction: column;
    align-items: flex-start;
    gap: 43px;
  }
  
  .explore-button {
    display: flex;
    padding: 16px 32px;
    align-items: center;
    gap: 16px;
    border-radius: 100px;
    background: #FFF;
    box-shadow: 1px;
    color: #F85E9F;
    font-size: 14px;
    font-family: Nunito Sans;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
  }
  
  .bag-icon {
    width: 24px;
    height: 24px;
  }
  
  h1 {
    display: flex;
    width: 370px;
    flex-direction: row;
    color: #000;
    font-size: 60px;
    font-family: Nunito Sans;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    text-align: left;
  }
  
  p {
    color: rgba(25, 24, 37, 0.50);
    font-size: 18px;
    font-family: Inter;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    text-align: left;
  }
  
  .button-group {
    display: flex;
    align-items: flex-start;
    gap: 16px;
  }
  
  .round-button1 {
    display: flex;
    padding: 16px 32px;
    align-items: flex-start;
    gap: 8px;
    border-radius: 100px;
    background: #5D50C6;
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.10), 0px 5px 11px 0px rgba(0, 0, 0, 0.10), 0px 20px 20px 0px rgba(0, 0, 0, 0.09), 0px 45px 27px 0px rgba(0, 0, 0, 0.05), 0px 81px 32px 0px rgba(0, 0, 0, 0.01), 0px 126px 35px 0px rgba(0, 0, 0, 0.00);
    color: #EEE;
    font-size: 14px;
    font-family: Nunito Sans;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
  }

  .round-button2 {
    display: flex;
    padding: 16px 32px;
    align-items: center;
    gap: 8px;
    border-radius: 100px;
    border: 1px solid #EEE;
    background: #FFF;
    color: #222831;
    font-size: 14px;
    font-family: Nunito Sans;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
  }

  .play-icon {
    width: 24px;
    height: 24px;
  }
  
  .round-button:last-child {
    margin-right: 0;
  }
  